import React, { useEffect, useState } from "react";
import Layout from "../containers/layout";
import CardListing from "../components/CardList";
import Ad from "../containers/Ad";
import { Heading, Box, Footer, Button, Card, Anchor } from "grommet";
import stores from "../../stores/index.json";
import { fetchStores } from "../helpers";
import { connect } from "react-redux";

const Home = ({ test }) => {
  const [stores, setStores] = useState([]);
  useEffect(() => {
    fetchStores().then((e) => setStores(e.result.items));
  }, []);
  test();
  return (
    <Layout max="1240px">
      <Box align="center" alignContent="center" justify="center">
        <CardListing
          dataList={stores.map((e) => ({
            title: e.name,
            description: e.description,
            cta: <Anchor href={`/store/${e.id}/${e.name}`}>Go to Store</Anchor>,
          }))}
        />
      </Box>
    </Layout>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    test: () => dispatch({ type: "test", payload: "test" }),
  };
};

export default connect(null, mapDispatchToProps)(Home);
